/* ==================================================================================
   Photo component
   ================================================================================== */
.photo-panel .photo-wrapper {
  position: relative;
  display: inline-block;
}
.photo-panel .photo-toolbar {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
}
.photo-panel .photo-edit {
  opacity: 0.8;
  filter: alpha(opacity=80);
  border-radius: 3px;
  background: #2eb2e4;
  color: white;
  border: 1px solid #1a96c5;
  width: 24px;
  height: 24px;
  text-align: center;
  vertical-align: middle;
  display: block;
}
.photo-panel .photo-edit:hover {
  opacity: 1;
  filter: alpha(opacity=100);
}
.photo-panel:hover .photo-toolbar {
  display: block;
}
